<!-- 分页组件 -->
<template>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="Number(total)"
        :current-page="pageNo"
        @current-change='currentChange'
        class="page"
        v-if="total > 10">
    </el-pagination>
</template>

<script>
export default {
    props: ['total','pageNo'],
    methods: {
        currentChange (page) {
            this.$emit('togglePages', page);
        }
    },
}
</script>

<style scoped lang='scss'>
.page{
    text-align: center;
    margin-top: 20px;
}
</style>