<template>
    <div>
        <!-- 顶部操作 -->
        <div class="baseTop">
            <el-button type="primary" @click="addRole">添加</el-button>
        </div>
        <!-- 列表数据 -->
        <el-table
            border
            :data="roleList"
            style="width: 100%">
            <el-table-column
                prop="roleName"
                label="角色">
            </el-table-column>
            <el-table-column
                prop="remark"
                label="角色描述"
                width="180">
            </el-table-column>
            <el-table-column 
                            label="角色类型"
                            width="180"
                            align='center'>
                <template slot-scope="scope">
                    {{scope.row.roleType == '1' ? '管理员' : '普通'}}
                </template>
            </el-table-column>
            <el-table-column 
                            label="操作"
                            width="300"
                            align='center'>
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        type="primary"
                        @click="editRole(scope.row)">编辑</el-button>
                    <el-button
                        size="mini"
                        @click="check(scope.row)">查看角色菜单</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 添加、编辑弹框 -->
        <el-dialog
            :title="!isAdd ? '添加' : '编辑'"
            :visible.sync="isShowAddDialog"
            width="50%"
            :before-close="cancel">
            <div class="addItem" v-if="isAdd">
                <span>角色Id</span>
                <el-input
                    placeholder="请输入角色Id"
                    v-model="roleId"
                    class="itemInput"
                    :disabled="isAdd">
                </el-input>
            </div>
            <div class="addItem">
                <span>角色名称</span>
                <el-input
                    placeholder="请输入角色名称"
                    v-model="roleName"
                    class="itemInput">
                </el-input>
            </div>
            <div class="addItem">
                <span>角色描述</span>
                <el-input
                    type="textarea"
                    :rows="2"
                    placeholder="请输入内容"
                    v-model="remark"
                    class="itemInput">
                </el-input>
            </div>
            <div class="addItem">
                <span>角色类型</span>
                <el-select 
                    v-model="roleType" 
                    placeholder="请选择"
                    clearable
                    class="itemInput">
                    <el-option label="管理员" value="1"></el-option>
                    <el-option label="普通" value="2"></el-option>
                </el-select>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="confirm">{{isAdd ? '编 辑' : '添 加'}}</el-button>
            </span>
        </el-dialog>
        <!-- 删除弹框 -->
        <el-dialog
            title="删除"
            :visible.sync="isShowDeleteDialog"
            width="30%"
            :before-close="cancel">
            <span v-if="itemData">是否确认删除<span class="tips">{{itemData.roleName}}</span>角色</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="danger" @click="deleteRole">删 除</el-button>
            </span>
        </el-dialog>
        <!-- 分页 -->
        <page :total='total' :pageNo='postData.pageNo' @togglePages='togglePages'></page>
    </div>
</template>

<script>
import { get_role_list_api, get_role_save_api, get_role_delete_api } from '@/api/system';
import page from '@/components/page';
export default {
    data() {	
        return {
            itemData: null, // 当前条数据
            isAdd: false, // 是否为添加
            roleId: '', // 角色id 
            roleName: '', // 角色名称
            roleType: '', // 角色类型
            remark: '', // 角色描述
            isShowDeleteDialog: false, // 是否显示删除框
            isShowAddDialog: false, // 是否显示删除框
            postData: {
                pageNo: 1,
                pageSize: 10
            },
            roleList: [], // 角色列表
            total: "" // 角色总条数
        }
    },
    methods: {
        // 校验
        verify() {
            if (this.roleName == '') {
                this.$message({
                    message: '请输入角色名称',
                    type: 'warning'
                });
                return
            }
            // if (this.remark == '') {
            //     this.$message({
            //         message: '请输入角色描述',
            //         type: 'warning'
            //     });
            //     return
            // }
            if (this.roleType == '') {
                this.$message({
                    message: '请选择角色类型',
                    type: 'warning'
                });
                return
            }
            let obj = {
                remark: this.remark,
                roleName: this.roleName,
                roleType: this.roleType,
            }
            // 如果是编辑就返回该字段
            if( this.isAdd ) {
                obj.roleId = this.roleId;
            }
            return obj;
        },
        // 获取数据
        getData() {
            get_role_list_api(this.postData).then(res => {
                this.roleList = res.list;
                this.total = res.total
            })
        },
        // 分页
        togglePages (page) {
            this.postData.pageNo = page;
            this.getData();
        },
        // 删除
        handleDelete (row) {
            this.itemData = row;
            this.isShowDeleteDialog = true;
        },
        // 查看
        check(row) {
            this.$router.push({
                path: `/roleMenu/${row.roleId}`
            })
        },
        // 编辑角色
        editRole(row) {
            this.roleId = row.roleId; 
            this.roleName = row.roleName;
            this.roleType = row.roleType == 1 ? '管理员' : '普通';
            this.remark = row.remark;
            this.isAdd = true;
            this.isShowAddDialog = true;
        },
        // 添加角色
        addRole () {
            this.isAdd = false;
            this.roleId = ''; 
            this.roleName = '';
            this.roleType = '';
            this.remark = '';
            this.isShowAddDialog = true;
        },
        // 取消
        cancel() {
            this.isShowDeleteDialog = false;
            this.isShowAddDialog = false;
        },
        // 确认添加与编辑
        confirm() {
            let obj = this.verify();
            if( obj ) {
                get_role_save_api(obj).then(() => {
                    if( this.isAdd ) {
                        this.formatData('编辑成功','success');
                    } else {
                        this.formatData('添加成功','success');
                    }
                    this.getData();
                })
            }
        },
        // 确认删除
        deleteRole() {
            get_role_delete_api({roleId:this.itemData.roleId}).then(() => {
                this.formatData('删除成功','success')
            })
        },
        // 格式化数据
        formatData(msg, type) {
            this.postData =  {
                pageNo: 1,
                pageSize: 10
            }
            this.$message({
                message: msg,
                type: type
            });
            this.isShowDeleteDialog = false;
            this.isShowAddDialog = false;
        }
    },
    created() {
        this.getData();
    },
    components: {
        page
    }
}
</script>

<style scoped lang='scss'>
.addItem{
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    span{
        display: block;
        width:150px;
    }
}
</style>